.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

.photo-card {
  background-color: #f2f2f2;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  display: flex;
  position: relative;
  box-shadow: 0 0 5px #0003;
}

.photo-card img {
  border-radius: 5px;
  width: 300px;
  height: 200px;
}

.photo-card:before {
  content: "";
  opacity: 0;
  background-color: #0000;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.photo-card:hover:before {
  opacity: 1;
  z-index: -1;
  background-color: #00000080;
}

.info {
  text-align: center;
  margin-top: 10px;
}

.info-item {
  margin: 5px 0;
}

.search-form {
  justify-content: center;
  width: 600px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
  display: flex;
}

.search-form input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  flex: 1;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.search-form .btn-search {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery_link:hover {
  opacity: .8;
  transition: opacity .3s;
}

.js-load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  justify-content: center;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: flex;
}

.js-load-more:hover {
  background-color: #0056b3;
}

.is-hidden {
  display: none;
}

